* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --tg-color-scheme: inherit;
  --tg-theme-bg-color: inherit;
  --tg-theme-button-color: inherit;
  --tg-theme-button-text-color: inherit;
  --tg-theme-hint-color: inherit;
  --tg-theme-link-color: inherit;
  --tg-theme-secondary-bg-color: inherit;
  --tg-theme-text-color: inherit;
  --tg-viewport-height: inherit;
  --tg-viewport-stable-height: inherit;
}

body {
  color: var(--tg-theme-text-color, white);
  background: var(--tg-theme-bg-color, black);
  font-family: Quicksand, sans-serif;
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

button {
  font-family: inherit;
  color: inherit;
}